import React, { useEffect, useState } from "react";
import Recipe from "./Recipe";
import home from './home.jpg';
import cheflogo from './cheflogo.png'
import "./App.css";

function App() {
  const API_ID = "38515c6b";
  const API_KEY = "58870541d04873b2cf9f2591c86a6c08";
  const [allRecipe, setRecipe] = useState([]);
  const [search, setSearch] = useState("");
  const [query, setQuery] = useState('chicken');
  useEffect(() => {
    getRecipe();
  }, [query]);

  const getRecipe = async () => {
    const getData = await fetch(
      `https://api.edamam.com/search?q=${query}&app_id=${API_ID}&app_key=${API_KEY}`
    );
    const finalData = await getData.json();
    setRecipe(finalData.hits);

  };
  const updateSearch = e => {
    setSearch(e.target.value)
  };
  const getSearch = e => {
    e.preventDefault();
    setQuery(search);
    setSearch('');
  };
  return (
    <div className="App">
      <div className="image-container">
        <img src={home} className="img-fluid" alt="Logo" />;
         <h5 className="logo">CookBook</h5>
        <img className="logoStyle" src={cheflogo}></img>
        <div className="titlehome">
          <h1 className="h1-title">Thinking of <br></br>what to <span className="spanTitle">Cook</span>?</h1>
          <h6>Find the best recipes at a click of a button from the largest
          <br></br>community. It is even better than an expensive <br></br>cookery book.</h6>
        </div>
      </ div>

      <h4 className="text-center mt-5 h4style">Search For Recipe</h4>
      <form onSubmit={getSearch} className="form">
        <input type="text" className="input" value={search} onChange={updateSearch}></input>
        <button type="submit" className="search">Search</button>
      </form>
      <h4 className="text-center mt-5 mb-4 h4style">Recipe List</h4>
      <div className="d-flex justify-content-around flex-wrap">
        {allRecipe.map((r) => (
          <Recipe
            key={r.recipe.label}
            title={r.recipe.label}
            label={r.recipe.label}
            calory={r.recipe.calories}
            image={r.recipe.image}
            ingredients={r.recipe.ingredients}
            url={r.recipe.url}
          />
        ))}
      </div>
    </div>
  );
}

export default App;
